import { createSlice } from "@reduxjs/toolkit";
import * as robo_service from "../services/Robo.service";

const robotBrowser = createSlice({
  name: "browser",
  initialState: {
    isLoading: true,
    selectedRobot: {},
    allRobots: [],
    filterdRobots: []
  },
  reducers: {
    changeLoadingStatus: (state, action) => ({ ...state, isLoading: false }),
    setAllRobots: (state, action) => ({ ...state, allRobots: action.payload }),
    setFilteredRobots: (state, action) => ({
      ...state,
      filterdRobots: action.payload
    }),
    setSelectedRobots: (state, action) => ({
      ...state,
      selectedRobot: action.payload
    }),
    addNewRobotToList: (state, action) => ({
      ...state,
      allRobots: [...state.allRobots, action.payload]
      // filterdRobots: [...state.allRobots, action.payload]
    })
  }
});

export default robotBrowser.reducer;
export const {
  changeLoadingStatus,
  setAllRobots,
  setFilteredRobots,
  setSelectedRobots,
  addNewRobotToList
} = robotBrowser.actions;
// console.log({ countSlice });
export const fetchRobots = () => {
  return async (dispatch) => {
    try {
      const data = await robo_service.get_all_robots();
      dispatch(setAllRobots(data));
      dispatch(setFilteredRobots(data));
      dispatch(setSelectedRobots(data[0]));
      dispatch(changeLoadingStatus());
    } catch (error) {
      console.log(error); // FAILED
    }
  };
};
