import { createStore, applyMiddleware } from "redux";
import { configureStore } from "@reduxjs/toolkit";

import { composeWithDevTools } from "redux-devtools-extension";
import thunk from "redux-thunk";
import rootReducer from "./reducers/rootReducer.reducer";

const logger = (store) => (next) => (action) => {
  console.log("dispatching", action); //, store.getState())
  console.log(store.getState());
  next(action);
  console.log("next state", store.getState());
};

const timer = (store) => (next) => (action) => {
  console.time("action"); //, store.getState())
  next(action);
  console.timeEnd("action");
};

// const middleware = applyMiddleware(logger, thunk);

// robo_service.get_all_robots();

// const store = createStore(rootReducer, composeWithDevTools(middleware));
const store = configureStore({
  reducer: rootReducer,
  // middleware: composeWithDevTools([logger, thunk])
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware().concat([logger, timer, thunk])
});

export default store;
