import errors from "./errors";

export const getFullNameErrorType = (value) => {
  if (!value) return [false, "required"];
  if (value.length < 2) return [false, "length"];
  return [true, ""];
};

export const getEmailErrorType = (value) => {
  const pattern = /\S+@\S+\.\S+/;
  if (!value) return [false, "required"];
  if (value.length < 8) return [false, "length"];
  if (!pattern.test(value)) return [false, "pattern"];

  return [true, ""];
};

export const getDescriptionErrorType = (value) => {
  if (!value) return [false, "required"];
  if (value.length <= 10) return [false, "length"];

  return [true, ""];
};

export const getCountryErrorType = (value) => {
  if (value !== "Pleace Select") return [true, ""];

  return [false, "required"];
};

export const getErrorMsg = (typeOfError, field) => {
  return errors[field][typeOfError];
};
