import React, { useState, useEffect, useRef } from "react";
import { useSelector, useDispatch } from "react-redux";
import styled from "styled-components";
import countries from "../countriesData/countries";
import { v4 as uuidv4 } from "uuid";

import {
  getFullNameErrorType,
  getErrorMsg,
  getEmailErrorType,
  getDescriptionErrorType,
  getCountryErrorType
} from "../errors/errorsUtils";
import { addNewRobotToList } from "../state/roboBrowser.slice";
import { useHistory } from "react-router-dom";

const CreateBot = () => {
  let history = useHistory();
  const dispatch = useDispatch();
  let [formSubmitOnce, setformSubmitOnce] = useState(false);
  const allCountries = useRef(Object.values(countries));
  const [fieldsValid, setFieldsValid] = useState({
    firstName: [false, "required"],
    lastName: [false, "required"],
    email: [false, "required"],
    country: [false, "required"],
    description: [false, "required"]
  });

  const [fieldsValues, setfieldsValues] = useState({
    firstName: "",
    lastName: "",
    email: "",
    country: "",
    description: ""
  });

  const addNewRobot = () => {
    let allValid = Object.values(fieldsValid).reduce(
      (acc, curr) => acc && curr[0],
      true
    );

    if (allValid) {
      const {
        country,
        firstName: first_name,
        lastName: last_name,
        email,
        description
      } = fieldsValues;
      console.log(country);

      const id = uuidv4();
      const newRobot = {
        avatar: `https://robohash.org/${id}`,
        country,
        description,
        email,
        first_name,
        id,
        last_name
      };
      dispatch(addNewRobotToList(newRobot));
      history.push("/browse");
    }
  };
  const handleSubmit = (e) => {
    e.preventDefault();
    setformSubmitOnce(true);
    addNewRobot();
  };

  const handleChange = (e, type) => {
    let newObj;
    let newValid;
    switch (type) {
      case "firstName":
        console.log("firstname change");
        let firstName = e.target.value;
        newObj = {
          ...fieldsValues,
          firstName
        };
        setfieldsValues(newObj);
        newValid = {
          ...fieldsValid,
          firstName: getFullNameErrorType(firstName)
        };
        setFieldsValid(newValid);

        break;
      case "lastName":
        console.log("lastname change");
        let lastName = e.target.value;
        newObj = {
          ...fieldsValues,
          lastName
        };
        setfieldsValues(newObj);
        newValid = {
          ...fieldsValid,
          lastName: getFullNameErrorType(lastName)
        };
        setFieldsValid(newValid);

        break;
      case "email":
        console.log("email change");
        let email = e.target.value;
        newObj = {
          ...fieldsValues,
          email
        };
        setfieldsValues(newObj);
        newValid = {
          ...fieldsValid,
          email: getEmailErrorType(email)
        };
        setFieldsValid(newValid);
        break;
      case "country":
        console.log("country change");
        let country = e.target.value.split(" ")[1];
        newObj = {
          ...fieldsValues,
          country
        };
        setfieldsValues(newObj);
        newValid = {
          ...fieldsValid,
          country: getCountryErrorType(country)
        };
        setFieldsValid(newValid);
        break;
      case "description":
        console.log("description change");
        let description = e.target.value;
        newObj = {
          ...fieldsValues,
          description
        };
        setfieldsValues(newObj);
        newValid = {
          ...fieldsValid,
          description: getDescriptionErrorType(description)
        };
        setFieldsValid(newValid);
        break;
      default:
        break;
    }
  };

  const renderCountries = () => {
    return (
      <ContrySelect onChange={(e) => handleChange(e, "country")}>
        <option>Pleace Select</option>
        {allCountries.current.map((country) => {
          return (
            <option>
              {country.emoji} {country.name}
            </option>
          );
        })}
      </ContrySelect>
    );
  };

  const getErrorMessege = (name) => {
    return getErrorMsg(fieldsValid[name][1], name);
  };

  return (
    <Form onSubmit={handleSubmit}>
      <Section>
        <Lable>Name</Lable>
        <FullName show={false}>
          <Input
            isValid={!fieldsValid.firstName[0] && formSubmitOnce}
            placeholder="First Name"
            onChange={(e) => handleChange(e, "firstName")}
          ></Input>
          <Input
            isValid={!fieldsValid.lastName[0] && formSubmitOnce}
            placeholder="Last Name"
            onChange={(e) => handleChange(e, "lastName")}
          ></Input>
        </FullName>
        {formSubmitOnce &&
          (!fieldsValid.firstName[0] || !fieldsValid.lastName[0]) && (
            <ErrorMsg>
              {fieldsValid.firstName[0]
                ? getErrorMessege("lastName")
                : getErrorMessege("firstName")}
            </ErrorMsg>
          )}
      </Section>
      <Section>
        <Lable>Email</Lable>
        <EmailInput
          isValid={!fieldsValid.email[0] && formSubmitOnce}
          placeholder="Email Adress"
          onChange={(e) => handleChange(e, "email")}
        ></EmailInput>
        {formSubmitOnce && !fieldsValid.email[0] && (
          <ErrorMsg>{getErrorMessege("email")}</ErrorMsg>
        )}
      </Section>
      <Section>
        <Lable>Country</Lable>
        <CountryBox isValid={!fieldsValid.country[0] && formSubmitOnce}>
          {renderCountries()}
        </CountryBox>

        {formSubmitOnce && !fieldsValid.country[0] && (
          <ErrorMsg>{getErrorMessege("country")}</ErrorMsg>
        )}
      </Section>
      <Section>
        <Lable>Description</Lable>
        <Description
          isValid={!fieldsValid.description[0] && formSubmitOnce}
          placeholder="A bit about da bot"
          onChange={(e) => handleChange(e, "description")}
        />
        {formSubmitOnce && !fieldsValid.description[0] && (
          <ErrorMsg>{getErrorMessege("description")}</ErrorMsg>
        )}
      </Section>
      <CreateButton>Create </CreateButton>
    </Form>
  );
};

export default CreateBot;

const Form = styled.form`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* align-items: flex-start; */
  padding: 3rem;

  min-height: 40rem;
  min-width: 40rem;
  background: white;
  border-radius: 6px;
  box-shadow: inset 0px 0px 14px 1px rgba(133, 133, 133, 1);
`;
const Section = styled.section`
  display: flex;
  flex-direction: column;
`;
const FullName = styled.section`
  display: flex;
`;
const Lable = styled.h5`
  font-size: 2.2rem;
  font-weight: inherit;
  color: darkslateblue;
`;
const Input = styled.input`
  height: 3.5rem;
  min-width: 24rem;
  outline: none;
  border-radius: 0.5rem;
  border: ${({ isValid }) => (isValid ? `red` : `white`)} 2px solid;
  color: ${({ isValid }) => (isValid ? `red` : `black`)};
  padding: 1rem;
  background: ${({ isValid }) => (isValid ? `pink` : `lightgray`)};
  font-size: 2rem;
  flex-basis: 50%;
`;
const EmailInput = styled(Input)`
  width: 100%;
`;
const Description = styled.textarea`
  width: 100%;
  font-size: 2rem;
  font-family: Arial;
  border-radius: 4px;
  padding: 1rem 2rem;
  border: ${({ isValid }) => (isValid ? `red` : `white`)} 2px solid;
  color: ${({ isValid }) => (isValid ? `red` : `slategray`)};
  outline: none;
  background: ${({ isValid }) => (isValid ? `pink` : `lightgray`)};
  color: slategray;
  align-self: stretch;
`;
const CreateButton = styled.button`
  background-color: pink;
  /* align-self: flex-end; */
  padding: 10px;
  margin-top: auto;
`;
const ErrorMsg = styled.h1`
  color: red;
  border: red dashed 1px;
  border-radius: 6px;
  display: ${({ show }) => (true ? "block" : "none")};

  align-self: center;
  text-align: center;
  padding: 0.6rem;
  width: 100%;
  margin: 1rem 0;
  font-size: 1.6rem;
  font-family: "Arial";
`;
const CountryBox = styled.div`
  height: 3.5rem;
  outline: none;
  border-radius: 0.5rem;
  border: ${({ isValid }) => (isValid ? `red` : `white`)} 2px solid;
  color: ${({ isValid }) => (isValid ? `red` : `black`)};
  padding: 1rem;
  background: ${({ isValid }) => (isValid ? `pink` : `lightgray`)};
  font-size: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
`;
const ContrySelect = styled.select`
  font-size: 2rem;
  font-family: "Arial";
  color: slategray;
  border: none;
  box-shadow: none;
  outline: none;
  background: transparent;
  appearance: none;
`;
